@import 'base';

.App {
    .container {
        scroll-snap-type: y mandatory;
    }
    
    .child {
        scroll-snap-align: start;
        scroll-snap-stop: always
    }
    
    section {
        height: 100vh;
        width: 100vw;
        scroll-snap-align: start;
        padding-top: 64px;
        overflow-y: hidden;
    }
}